// COMMON :: START

.lang {
	position: relative;

	&:hover {
		.lang {
			&__toggle {
				border-color: #fff;
				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;
				background-color: #ffffff;
				@include transition(border-color, background-color, border-radius);

				span,
				.icon-font {
					color: #080915;
					@include transition(color);
				}
			}

			&__drop {
				opacity: 1;
				visibility: visible;
				transform: translateY(0);
				@include transition(opacity, visibility, transform);
			}
		}
	}

	&__toggle {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 84px;
		height: 29px;
		padding-left: 10px;
		padding-right: 10px;
		border-width: 2px;
		border-color: rgb(48, 51, 90);
		border-style: solid;
		border-radius: 5px;
		background-color: transparent;
		@include transition(border-color, background-color, border-radius);

		span,
		.icon-font {
			color: #9294ad;
			@include transition(color);
		}

		span {
			font-weight: 700;
			text-transform: uppercase;
		}

		.icon-font {
			margin-left: 10px;
			font-size: 8px;
			transform: rotate(-90deg);
		}
	}

	&__drop {
		opacity: 0;
		visibility: hidden;
		transform: translateY(10px);
		z-index: 999;
		position: absolute;
		top: 100%;
		right: 0;
		display: flex;
		min-width: 350px;
		border-radius: 5px 0 5px 5px;
		background-color: rgb(255, 255, 255);
		backface-visibility: hidden;
		transform-origin: center;
		@include transition(opacity, visibility, transform);

		&-col {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			width: 50%;
			padding: 15px;
		}

		&-link {
			position: relative;
			display: flex;
			align-items: center;
			padding-top: 5px;
			margin-bottom: 5px;

			&:hover {
				&:before,
				&:after {
					width: 50%;
					@include transition(width);
				}
			}

			&:before,
			&:after {
				content: '';
				position: absolute;
				top: 100%;
				width: 0;
				height: 1px;
				background-color: #080915;
				@include transition(width);
			}

			&:before {
				left: 50%;
			}
			&:after {
				right: 50%;
			}

			span {
				color: #080915;
			}
		}
	}

	svg {
		margin-right: 5px;
		width: 19px;
		height: 18px;
	}
}

.category {
	position: relative;
	display: flex;
	padding-top: 10px;
	padding-bottom: 10px;

	> * {
		height: 34px;
	}

	&__left {
		width: 43px;
	}

	&__middle {
		position: relative;
		flex: 1;
		min-width: 1px;
		max-width: 100%;
		padding-right: 4px;
	}

	&__right {
		width: 111px;
	}

	&__btn {
		outline: none;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 34px;
		border-radius: 5px;
		border: none;
		box-shadow: none;
		background-color: rgb(0, 74, 172);
		text-transform: uppercase;
		color: #ffffff;
		font-size: 13px;
		font-weight: 700;
	}

	&__wrapper {
		position: relative;
		display: flex;
		width: 100%;
		height: 100%;
	}

	&__toggle {
		position: relative;
		display: flex;
		width: 100%;
		height: 100%;
		background-color: #ffffff;
		border-radius: 0 5px 5px 0;

		input {
			outline: none;
			position: relative;
			flex: 1;
			min-width: 1px;
			max-width: 100%;
			height: 100%;
			padding: 0 12px;
			border: none;
			box-shadow: none;
			border-radius: 0;
			background-color: transparent;
			font-size: 14px;
			font-weight: 700;
			@include inputPlaceholder {
				color: #9294ad;
				font-size: 14px;
				font-weight: 700;
			}
		}

		button {
			outline: none;
			cursor: pointer;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 40px;
			height: 100%;

			border: none;
			border-radius: 0;
			box-shadow: none;
			background-color: transparent;

			.icon-font {
				color: #9294ad;
			}
		}
	}
}

.sort {
	position: relative;
	width: 100%;
	height: 100%;

	&__toggle {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		border-radius: 5px 0 0 5px;
		background-color: #939cb2;

		.icon-font {
			color: #ffffff;
			font-size: 20px;
			transform: rotate(-90deg);
		}

		p {
			display: flex;
			flex-direction: column;
			transform: translateX(-3px);

			span {
				font-weight: 700;
				font-size: 12px;
				color: #ffffff;
				line-height: 1;
			}
		}
	}
}

.copyright {
	display: flex;
	align-items: center;
	justify-content: center;

	&__text {
		font-size: 22px;
		color: rgb(135, 144, 176);
		line-height: 2.273;
	}
}

// COMMON :: END
